.template {
  padding-top: 80px;
  font-size: 16px;
  line-height: 150%;
}
.template__title {
  margin-bottom: 20px;
}
.template h1 {
  font-size: 48px;
  font-style: normal;
  font-weight: 500;
  line-height: 64px;
  text-transform: uppercase;
  margin-bottom: 20px;
}
.template h2 {
  font-size: 40px;
  font-style: normal;
  font-weight: 500;
  line-height: 48px;
  text-transform: uppercase;
  margin-top: 20px;
  margin-bottom: 20px;
}
.template h3 {
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.template p {
  margin-top: 20px;
  margin-bottom: 20px;
}
.template strong,
.template b {
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  margin-bottom: 24px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.template ul {
  margin-top: 20px;
  margin-bottom: 20px;
}
.template ul li {
  padding-left: 20px;
  position: relative;
}
.template ul li:not(:last-child) {
  margin-bottom: 12px;
}
.template ul li::before {
  content: "";
  position: absolute;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #414F5C;
  left: 0;
  top: 10px;
}
.template ol {
  margin-top: 20px;
  margin-bottom: 20px;
  list-style-type: none;
  counter-reset: num;
}
.template ol li {
  padding-left: 20px;
  position: relative;
}
.template ol li:not(:last-child) {
  margin-bottom: 12px;
}
.template ol li::before {
  content: counter(num) ".";
  counter-increment: num;
  position: absolute;
  left: 0;
  top: 0px;
}
.template img {
  margin-top: 20px;
  margin-bottom: 20px;
}

